body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: teal;
  color: azure;
}
footer a{
  color: greenyellow;
}
.navbar-expand>.container{
  flex-wrap: wrap;
}
.navigation, footer {
  background-color: black;
  align-items: center;
  width: auto;
}
.siteTitle{
  color: greenyellow;
  font-style: italic;
}
a.nav-link{
  color: greenyellow;
}
a {
  padding-left: 5px;
  color:tomato;
}
.HairOptions {
  align-items: center;
}
h3 {
  text-decoration: underline;
}
ul {
  margin: auto;
}
@media screen and (min-width: 750px) {
  .nailPhotos img, .facialPhotos img{
  height: 350px;
  width: 250px;
}
.eyelashPhotos img{
  height: auto;
  width: 500px;
}
.BodyWorkPhotos img, .browCarePhotos img, .teethPhotos img, .MakeupPhotos img {
  height: 300px;
  width: 450px;
}
.navbar-expand>.container{
  flex-direction: row;
}
.navbar-expand .navbar-nav {
  flex-direction: row;
}
}
@media screen and (max-width: 750px){
.navbar-expand>.container{
  flex-direction: column;
}
.navbar-expand .navbar-nav {
  flex-direction: column;
}
}
.employees-and-bios img {
  height: auto;
  width: 200px;
}
p {
  margin-bottom: auto;
}
.grid-container {
  padding-left: .25rem;
  padding-right: .25rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
